.aboutme {
	background-color: $color_bg_grey;
	position: relative;
	overflow: hidden;

	&__conteiner {
		padding-top: 30px;
		display: grid;
		grid-template-columns: 1fr 1fr 1.5fr; }

	&__text {
		display: flex;
		grid-column: 1 / 3;
		z-index: 1; }

	&__photo {
		grid-column: 3 / 4;
		z-index: 1;
		justify-self: end; }


	&__ellipse {
		background-image: url(../../img/ellipse.svg);
		background-repeat: no-repeat;
		background-position-x: 59vw;
		background-position-y: 23px;
		position: absolute;
		width: 100vw;
		height: 100%; }

	&__title {
		margin-bottom: 18px;
		font-size: calc(30px + 3vw);
		font-weight: 900;
		color: $color_html; }

	&__subtitle {
		margin-bottom: 45px;
		font-size: calc(12px + 1vw);
		color: $color_grey; }

	&__text-conteiner {
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		margin-top: 105px; } }


.photo {
	display: flex;

	&__img {
		margin-right: -80px; } }


.button-orange {
	display: block;
	width: 270px;
	height: 60px;
	border: none;
	background-color: $color_orange;
	font-size: 18px;
	color: #fff;
	text-align: center;
	line-height: 60px;
	transition: background $time_hover, color $time_hover;

	&:hover {
		background-color: $color_pink; } }

.animate {
	position: absolute; }

.main-title {
	text-align: center;
	font-size: calc(30px + 1.6vw); }

.skills {
	padding-top: 86px;

	&__conteiner {
		display: grid;
		grid-template-columns: 1fr 1fr 1fr 1fr;
		grid-template-rows: 158px;
		grid-column-gap: 20px;
		margin-top: 85px;
		padding-bottom: 86px; }

	&__item {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: center; }

	&__img {
		width: 74px;
		height: 74px; }

	&__p {
		text-align: center;
		font-size: calc(10px + 0.7vw);
		color: $color_grey;
		line-height: 33px; } }

.button_center {
	margin: 0 auto; }

.portfolio {
	background-color: $color_bg_grey;
	margin-top: 102px;
	padding-top: 93px;
	padding-bottom: 100px; }

.portfolio__conteiner {
	padding-left: 40px;
	padding-right: 40px; }

.portfolio__slider {
	width: 100%;
	padding: 0 30px 93px; }

.portfolio__header {
	margin-top: 30px;
	text-align: center;
	font-size: 20px;
	font-weight: 400;
	color: $color_grey; }

.slider {
	margin-top: 100px;
	margin-bottom: 105px; }

.owl-dots {
	text-align: center;
	margin-top: 10px; }

.owl-dot {
	width: 18px;
	height: 18px;
	border-radius: 50%;
	margin: 0 4px;
	background-color: #ebebeb; }

button.active {
	background: $color_orange; }

.owl-nav {
	position: relative; }

.owl-nav button {
	position: absolute;
	top: -197px;
	width: 60px;
	height: 60px;
	border-radius: 50%;
	box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.17); }

.owl-nav button:hover {
	box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.35); }

button.owl-prev {
	left: -32px;
	background: #fff; }

button.owl-next {
	right: -32px;
	background: #fff; }

.prev {
	background: url("../../img/prev.svg") center no-repeat;
	background-position: 50% 50%;
	width: 100%;
	height: 100%;
	background-size: 23px 30px; }

.next {
	background: url("../../img/next.svg") center no-repeat;
	background-position: 50% 50%;
	width: 100%;
	height: 100%;
	background-size: 23px 30px; }






.price {
	background: $color_bg_gradient;

	&__header {
		font-size: 44px;
		font-weight: 700;
		color: #fff;
		line-height: 57px; }

 &__conteiner {
		padding: 80px 133px 2px 133px; }

	&__small {
		display: flex;
		font-size: 25px;
		font-weight: 300;
		line-height: 27px;
		margin-top: 37px;
		margin-bottom: 78px;
		color: #fff;

		&:before {
			content: '\002A';
			margin-right: 5px; } } }






