@media screen and (min-width: 1500px) {
	.photo__img {
		margin-right: -180px; }

	.skills__p {
		font-size: 20px; }

	.main-title {
		font-size: 54px; } }


@media screen and (min-width: 1440px) {
	.aboutme__title {
		font-size: 80px; }

	.aboutme__subtitle {
		font-size: 25px; } }



@media screen and (max-width: 1024px) {
	.conteiner {
		max-width: 990px; }

	.burger {
		display: block; }

	.header__nav_tablet {
		position: absolute;
		top: 0;
		left: -50vh;
		width: 40vh;
		height: 100vh;
		transition: transform $time_menu cubic-bezier(.17,.67,.25,.98);
		background-color: $color_backing;
		z-index: 3; }

	.overlay {
		position: absolute;
		top: 0;
		left: 0;
		width: 100vw;
		height: 100vh;
		background-color: rgba(0,0,0,.78);
		transition: transform $time_menu linear;
		z-index: 2; }

	.menu__link_tablet {
		color: #fff;
		font-size: 26px; }

	.header__nav_click {
		transform: translateX(50vh); }

	.menu__tablet {
		flex-direction: column;
		margin-top: 50px; }

	.menu__item_tablet {
		margin: 20px auto; }





	.aboutme__title {
		font-size: calc(30px + 2vw);
		margin-bottom: 25px; }

	.aboutme__text-conteiner {
		margin-top: 150px; }

	.button-orange {
		width: 320px;
		height: 68px;
		line-height: 68px; }

	.aboutme__ellipse {
		background-position-x: 47vw;
		background-position-y: 110px; }

	.photo__img {
		margin-right: 0; }




	.skills__conteiner {
		grid-template-columns: 1fr 1fr;
		grid-template-rows: 1fr 1fr;
		grid-template-rows: 158px 158px;
		grid-column-gap: 20px;
		grid-row-gap: 75px;
		margin-top: 85px; }

	.skills__p {
		font-size: calc(10px + 0.7vw); }

	.price__conteiner {
		padding: 80px 20px 2px 20px; } }



@media screen and (max-width: 900px) {
	.aboutme__text-conteiner {
		margin-top: 0;
		align-items: center;
		width: 100vw; }

	.aboutme__text {
		grid-column: 1/4;
		grid-row: 2/2;
		justify-self: center;
		margin-top: 0;
		background-color: #fff; }


	.aboutme__photo {
		grid-column: 1/4;
		grid-row: 1/2;
		justify-self: center; }

	.aboutme__ellipse {
		background-position-x: 0;
		background-position-y: 136px; }

	.photo__img {
		margin-right: -59px; } }






@media screen and (max-width: 768px) {
	.header {
		height: 77px;

		&__button {
			width: 45px;
			height: 45px;
			border-radius: 50%;
			border: $border_pink;
			font-size: 0; }

		&__button:hover {
			background-color: transparent; }

		&__phone:hover {
			color: $color_html; } }

	.menu__link:hover {
		color: #fff; }

	.menu__link_tablet {
		font-size: 20px; }

	.icon-phone__header:before {
		font-size: 20px; }



	.aboutme__subtitle {
		font-size: 16px;
		padding: 0 10px;
		text-align: center; }

	.aboutme__ellipse {
		background-position-x: -13vw;
		background-position-y: 136px; } }






@media screen and (max-width: 500px) {
	.aboutme__ellipse {
		background-position-x: -13vw;
		background-position-y: 77px;
		background-size: 137%; }


	.skills__conteiner {
		grid-template-columns: 1fr;
		grid-template-rows: 1fr 1fr 1fr 1fr;
		grid-template-rows: 158px 158px 158px 158px;
		grid-row-gap: 75px;
		margin-top: 85px; }

	.skills__p {
		font-size: 16px; } }




@media screen and (max-width: 425px) {

	.burger {
		height: 35px;
		width: 40px;
		margin-left: -8px;

		&__btn span,
		&__btn span::before,
		&__btn span::after {
			width: 25px;
			height: 3px; }

		&__btn span {
			left: 8px;
			top: 17px; }

		&__btn span::after {
			top: -8px; }

		&__btn span::before {
			top: 8px; }

		&__btn_click span::after,
		&__btn_click span::before {
			top: 0; }

		&__btn_click span {
			width: 0;
			margin-left: 0; } }

	.header__phone {
		font-size: 18px;
		margin-left: 0;
		margin-right: 0; }

	.header__button {
		width: 26px;
		height: 26px; }

	.icon-phone__header:before {
		font-size: 14px; }





	.aboutme__conteiner {
		padding-top: 57px; }

	.aboutme__title {
		font-size: 30px;
		margin-top: 12px;
		margin-bottom: 12px; }

	.aboutme__subtitle {
		margin-bottom: 25px;
		line-height: 22px; }

	.button-orange {
		width: 280px;
		height: 55px;
		line-height: 55px;
		font-size: 16px; }





	.skills {
		padding-top: 44px;

		&__conteiner {
			margin-top: 40px;
			grid-row-gap: 12px; }

		&__item {
			justify-content: start; }

		&__img {
			width: 56px;
			height: 60px; }

		&__p {
			line-height: 22px;
			padding-top: 18px; }

		&__button {
			margin: 19px auto 0; } }

	.main-title {
		font-size: 30px; }

	.portfolio__conteiner {
		padding-left: 20px;
		padding-right: 20px; } }


